.section {
  padding: 120px 0px;
}
.skills-item b {
  color: #f44336;
}
.skills-home {
  position: relative;
}

.skills-item i {
  font-size: 50px;
  float: left;
  width: 65px;
  height: 120px;
}
.shake {
  animation: shake-animation 4.72s ease infinite;
  transform-origin: 50% 50%;
}
@keyframes shake-animation {
  0% {
    transform: translate(0, 0);
    color: #f44336;
  }
  1.78571% {
    transform: translate(5px, 0);
  }
  3.57143% {
    transform: translate(0, 0);
  }
  5.35714% {
    transform: translate(5px, 0);
  }
  7.14286% {
    transform: translate(0, 0);
  }
  8.92857% {
    transform: translate(5px, 0);
  }
  10.71429% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}
.skills-item i:hover {
  color: #f44336;
}
